.app {
  display: grid;
  grid-gap: 1rem;
  gap: 1rem;
  grid-template-columns: repeat(3, 1fr);
  grid-template-areas: "header header header header" "contents contents contents sidebar" "footer footer footer footer";
  background-color: white;
  width: 1024px;
  max-width: 100%;
  margin: 10px auto; }
  .app .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    grid-area: header; }
  .app .sidebar {
    grid-area: sidebar; }
  .app .contents {
    grid-area: contents;
    align-items: center;
    max-width: 100%; }
  .app .footer {
    grid-area: footer;
    justify-self: center; }



.suggestion {
  display: grid;
  grid-template-columns: 32px 2fr 1fr;
  align-items: center;
  margin-bottom: 0.5em; }
  .suggestion .avatar {
    justify-self: center; }
  .suggestion .action {
    justify-self: right; }


