.app {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
    grid-template-areas:
        "header header header header"
        "contents contents contents sidebar"
        "footer footer footer footer";
    background-color: white;
    // border: 3px solid #333;
    width: 1024px;
    max-width: 100%;
    margin: 10px auto;

    .header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        grid-area: header;
    }

    .page-title {}

    .search {}

    .topnav {}

    .sidebar {
        grid-area: sidebar;
    }

    .contents {
        grid-area: contents;
        align-items: center;
        max-width: 100%;
    }

    .footer {
        grid-area: footer;
        justify-self: center;
    }
}